.site-header {
    margin: 20px 0;
    text-align: center;
}

.pixel-title {
    font-size: 36px;
    margin: 20px;
    color: #cba6f7; /* Lavender */
}

/* Hamburger menu styles */
.hamburger {
    font-size: 30px;
    background: none;
    border: none;
    color: #cdd6f4;
    cursor: pointer;
    display: none; /* Hidden by default */
}

/* Nav styles */
.pixel-nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* Wraps items on smaller screens */
}

.pixel-nav li {
    display: inline-block;
    margin: 0 10px;
}

.pixel-nav a {
    text-decoration: none;
    color: #cdd6f4; /* Rosewater */
    padding: 10px 20px;
    border: 2px solid #cba6f7; /* Pink border */
    background-color: #181825; /* Light blue background */
    border-radius: 4px;
    transition: all 0.3s;
}

/* Hover styles specific to sections */
.pixel-nav a.about-link:hover {
    background-color: #f38ba8; /* Match About section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.languages-link:hover {
    background-color: #fab387; /* Match Languages section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.tools-link:hover {
    background-color: #f9e2af; /* Match Tools section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.contact-link:hover {
    background-color: #94e2d5; /* Match Contact section h2 color */
    color: #1e1e2e;
}

/* Responsive Styles */
@media (max-width: 768px) {
    /* Show hamburger menu on small screens */
    .hamburger {
        display: inline-block;
        margin: 20px 0;
    }

    /* Hide nav by default on small screens */
    .pixel-nav ul {
        display: none;
        flex-direction: column;
        background-color: #1e1e2e;
        padding: 20px;
        margin-top: 20px;
        position: relative;
        width: 100%;
        /* box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2); */
    }

    /* Show nav when open */
    .pixel-nav.open ul {
        display: flex;
        padding: 0 10;
    }

    /* Adjust nav items for mobile */
    .pixel-nav li {
        margin: 20px 0;
    }
}
